import Vue from 'vue'
import Vuex from 'vuex'
import {
    cacheState,
    compile,
    downloadProject,
    downloadScript,
    loadProjectFromFile,
    loadProjectFromCache,
    exportToText,
    importFromText,
} from './transfer'
import {
    createResource,
    createRole,
    createChunk,
    removeResource,
    removeRole,
    removeChunk,
    updateData,
} from './data-mutation'
import { v4 as genId } from 'uuid';
import {
    openPanel,
    closePanel,
    closeAllPanels,
    savePanel,
    saveAllPanels,
    bindEl,
} from "./panel";
import {
    openDialog,
    closeDialog,
} from "./dialog";

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        id: genId(), // ID
        lastModified: Date.now(), // 
        name: '', // 
        version: '1.0.0', // 
        authors: [], // 
        entry: null, // 
        data: {}, // 
        resources: [], // 
        roles: [], // 
        chunks: [], // 

        script: null, // 

        panels: [],
        tabIndex: -1,

        app: null,

        messages: [],

        dialog: null,
    },
    mutations: {
        openPanel,
        closePanel,
        closeAllPanels,
        savePanel,
        saveAllPanels,
        bindEl,

        openDialog,
        closeDialog,

        cacheState,
        compile,
        downloadProject,
        downloadScript,
        loadProjectFromFile,
        loadProjectFromCache,


        createResource,
        createRole,
        createChunk,

        removeResource,
        removeRole,
        removeChunk,

        updateData,

        exportToText,
        importFromText,

        updateBaseInfo(state, { name, version, authors }) {
            state.name = name || state.name;
            state.version = version || state.version;
            state.authors = authors || state.authors;
        },

        setTabIndex(state, index) {
            state.tabIndex = index;
        },

        setApp(state, app) {
            state.app = app;
        },
    },
})
