<template>
    <!--  -->
    <div class="fill-y max-width-64em pa-5 d-flex">
        <div class="form d-flex flex-column align-center">
            <!--  -->
            <v-avatar 
                size="160"
                color="grey"
            >
                <img v-if="avatar" :src="avatarUrl" />

                <span v-else class="white--text text-h3">{{ name.slice(0, 2) }}</span>
            </v-avatar>

            <!--  -->
            <v-text-field
                label=""
                class="input flex-grow-0"
                messages=""
                placeholder=""
                v-model="name"
            />

            <!--  -->
            <v-switch 
                class="flex-grow-0" 
                label="" 
                v-model="noPic" 
            />

            <!--  -->
            <ResSelector 
                class="flex-grow-0" 
                label=""
                messages="" 
                v-model="avatar" 
            />

            <!--  -->
            <ResSelector 
                class="flex-grow-0" 
                label=""
                :disabled="noPic" 
                :messages="noPic ? '' : ''"
                v-model="pic" 
            />

            <v-spacer/>

            <!--  -->
            <v-btn color="warning" @click="deleteSelf">
                <v-icon>mdi-delete</v-icon>
                
            </v-btn>
        </div>

        <!--  -->
        <div class="img-wrapper flex-grow-1">
            <img 
                v-if="!noPic && pic" 
                :alt="name" 
                :src="picUrl"
            />
        </div>
    </div>
</template>

<script>
import ResSelector from "../ResSelector.vue";
import { mapMutations, mapState } from "vuex";
import { mutateWatcher } from '../../utils/vue-util'


export default {
    name: "Role",

    components: {
        ResSelector,
    },

    props: {
        id: String,
    },

    data() {
        const role = this.$store.state.data[this.id];
        return {
            name: role.name || '',
            avatar: role.avatar || null,
            pic: role.pic || null,
            noPic: role.noPic || false,
        };
    },

    watch: {
        ...mutateWatcher(null, 'name', 'avatar', 'pic', 'noPic'),
    },

    computed: {
        ...mapState(["data"]),

        picUrl() {
            const res = this.data[this.pic];
            return res ? res.src : null;
        },

        avatarUrl() {
            const res = this.data[this.avatar];
            return res ? res.src : null;
        },
    },

    methods: {
        ...mapMutations(['updateData', 'removeRole']),

        deleteSelf() {
            this.removeRole(this.id);
            this.$emit('delete', this.id);
        },

        save() {
            this.updateData({
                id: this.id,
                name: this.name,
                avatar: this.avatar,
                pic: this.pic,
                noPic: this.noPic,
            });
        },
    },

    created() {
        this.$store.commit('bindEl', { id: this.id, el: this });
    },
};
</script>

<style lang="scss" scoped>
.img-wrapper {
    flex: 1;
    text-align: center;

    img {
        max-width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center;
    }
}

.form {
    > * {
        width: 20em;
        margin-top: 1.5em;
    }
}
</style>