<template>
    <v-card>
        <v-container>
            <v-row>
                <v-select
                    label=""
                    chips
                    deletable-chips
                    multiple
                    :items="chunkList"
                    item-value="id"
                    item-text="title"
                    v-model="selectedChunks"
                />
            </v-row>

            <v-row class="d-flex justify-space-around">
                <v-switch label="" v-model="withName" />

                <v-switch
                    label=""
                    hint=""
                    v-model="reduceName"
                    :disabled="!withName"
                />
            </v-row>

            <v-row class="d-flex justify-space-around">
                <v-switch label="" v-model="withTitle" />

                <v-switch
                    label=""
                    hint=""
                    v-model="quotedTitle"
                    :disabled="!withTitle"
                />
                
                <v-switch label="" v-model="withSubtitle" />
            </v-row>

            <v-row class="d-flex justify-space-around">
                <v-btn color="primary" @click="doExport">
                    <v-icon>mdi-export</v-icon>
                </v-btn>

                <v-btn @click="closeDialog">
                    <v-icon>mdi-cancel</v-icon>
                </v-btn>
            </v-row>
        </v-container>
    </v-card>
</template>

<script>
import { mapMutations, mapState } from "vuex";
export default {
    name: "TextExport",

    data() {
        return {
            selectedChunks: [],
            withName: true,
            reduceName: true,
            quotedTitle: true,
            withTitle: true,
            withSubtitle: true,
        };
    },

    computed: {
        ...mapState(["data", "chunks"]),

        chunkList() {
            return this.chunks.map((id) => this.data[id]);
        },
    },

    methods: {
        ...mapMutations(["exportToText", "closeDialog"]),

        doExport() {
            this.exportToText({
                selectedChunks: this.selectedChunks,
                withName: this.withName,
                reduceName: this.reduceName,
                quotedTitle: this.quotedTitle,
                withTitle: this.withTitle,
                withSubtitle: this.withSubtitle,
            });
            this.closeDialog();
        },
    },
};
</script>