<template>
    <v-container>
        <h3></h3>

        <v-subheader>ID{{ $store.state.id }}</v-subheader>

        <v-subheader>{{ lastModified | localizeDate }}</v-subheader>

        <v-text-field
            label=""
            v-model="name"
        />

        <v-text-field
            label=""
            v-model="version"
        />

        <v-combobox
            chips
            multiple
            deletable-chips
            label=""
            v-model="authors"
        />
    </v-container>
</template>

<script>
import { mapMutations, mapState } from 'vuex';
import { mutateWatcher } from '../../utils/vue-util.js'

export default {
    name: "BaseInfo",

    props: {
        id: String,
    },

    data() {
        return {
            name: this.$store.state.name,
            version: this.$store.state.version,
            authors: this.$store.state.authors,
            entry: this.$store.state.entry,
        };
    },

    computed: {
        ...mapState(['lastModified']),
    },

    watch: {
        ...mutateWatcher(null, 'name', 'version', 'authors', 'entry'),
    },

    methods: {
        ...mapMutations(['updateBaseInfo']),

        save() {
            this.updateBaseInfo({
                name: this.name, 
                version: this.version,
                authors: this.authors,
                entry: this.entry,
            });
        },

        update() {
            this.name = this.$store.state.name;
            this.version = this.$store.state.version;
            this.authors = this.$store.state.authors;
            this.entry = this.$store.state.entry;
        },
    },

    created() {
        this.$store.commit('bindEl', { id: this.id, el: this });
    },
};
</script>
